body {
  padding-top: 56px;
  background: linear-gradient(#2c2c44 50%, #34364f 50%);
}

header {
  background-image: url(../../assets/pow-1601674_1920.png);
  background-position: right;
  background-repeat: no-repeat;
  background-size: contain;
  background-attachment: fixed;
  background-size: 40%;
  background-position-x: 600px;
  background-position-y: 35px;
}
header p {
  width: 65%;
  font-family: "Akaya Telivigala", cursive;
}
h1 {
  background-image: linear-gradient(#2c2c44 50%, red 50%);
  -webkit-background-clip: text;
  color: transparent;
  font-family: "DotGothic16", sans-serif;
}
#long-description {
  max-height: 100px;
  overflow-y: auto;
}
.btn-primary {
  background: linear-gradient(to right, #2c2c44, purple);
}
.btn {
  font-family: "Akaya Telivigala", cursive;
}
.btn-primary:hover {
  transform: scale(1.2);
}
.btn-danger {
  background: linear-gradient(45deg, grey, red);
}
.btn-danger:hover {
  transform: scale(1.2);
}

#btn-white {
  background-color: #f1f1f1;
}

#btn-white:hover {
  background: linear-gradient(50deg, blue, yellow);
  color: white;
}

#all-card {
  /* background: linear-gradient(50deg, yellow, blue); 
  color: #F1F1F1; */
}
#all-card p {
  font-family: "Kaushan Script", cursive;
}
#image {
  background-size: cover;
}
h4 {
  font-family: "Permanent Marker", cursive;
  background: linear-gradient(50deg, blue, yellow);
  -webkit-background-clip: text;
  color: transparent;
}

/* ------header------ */

@media (min-width: 1400px) {
  header {
    background-size: 30%;
    background-position-x: 900px;
    background-position-y: 100px;
  }
}

@media (max-width: 1400px) {
  header {
    background-size: 30%;
    background-position-x: 900px;
    background-position-y: 100px;
  }

  header p {
    width: 55%;
  }
}

@media (max-width: 1200px) {
  header {
    background-attachment: fixed;
    background-size: 50%;
    background-position-x: 500px;
    background-position-y: 140px;
  }
  header h1 {
    font-size: 0.5r;
  }

  header p {
    width: 30%;
  }
}

@media (max-width: 991px) {
  header {
    background-size: 45%;
    background-position-y: 200px;
    background-position-x: 350px;
  }
}

@media (max-width: 800px) {
  header {
    background-size: 60%;
    background-position-y: 180px;
    background-position-x: 250px;
  }
}

@media (max-width: 768px) {
  header {
    background-repeat: repeat-y;
    background-size: 40%;

    background-position-x: 350px;
  }
  header h1 {
    width: 50%;
  }
  header p {
    width: 40%;
  }
}

@media (max-width: 426px) {
  header {
    background-image: none;
  }
  header h1 {
    width: 100%;
  }
  header p {
    width: 100%;
  }
}
